import './TakeAction.css';
import Toolbar from '../../components/Toolbar/Toolbar';
import React, { Component } from 'react';
import SideDrawer from '../../components/SideDrawer/SideDrawer';
import Backdrop from '../../components/Backdrop/Backdrop';
import InfoEatwell from '../../components/InfoEatwell/InfoEatwell';
import InfoAvoidplastic from '../../components/InfoAvoidplastic/InfoAvoidplastic';
import InfoInvestsmarter from '../../components/InfoInvestsmarter/InfoInvestsmarter';
import InfoKeepcool from '../../components/InfoKeepcool/InfoKeepcool';
import InfoProtectyourhome from '../../components/InfoProtectyourhome/InfoProtectyourhome';
import InfoSaveenergy from '../../components/InfoSaveenergy/InfoSaveenergy';
import InfoVote from '../../components/InfoVote/InfoVote';
import AskTakeAction from '../../components/AskTakeAction/AskTakeAction';
import SideMenuTakeAction from '../../components/SideMenuTakeAction/SideMenuTakeAction';
import NearSpaceBg from '../../components/NearSpaceBg/NearSpaceBg';
import styled from 'styled-components';
import {Link} from 'react-router-dom';

const Tree = styled.p`
  display:flex;
  flex-direction:row;
  flex-wrap:wrap;
  font-family:roboto;
  font-size: 1.5rem;
  font-weight:300;
  color: #ffffff;
  margin: 0em 0em 0.9em;
  letter-spacing: .6px;
  margin-left:0px;
  
`;

const Stacker= styled.div`
  display: flex;
  flex-direction:row;
  height: auto;
  justify-content: space-around;
  @media (max-width: 1130px) {
    flex-direction: column;
  }
`;
const Left = styled.div`
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  display:flex;
  justify-content:center;
`;

const Threat = styled.div`
    width:40vw;
  align-self:center;
  @media (max-width: 1130px) {
    width:85%;
  }
   @media (max-width: 768px) {
    width:100%;
  }
`;

const Right = styled.div`
    display:flex;
  justify-content:center;
`;

const Impacts = styled.div`
  width:40vw;
  flex-direction:column;
  justify-content:center;

    @media (max-width: 1130px) {
    width:85%;
  }
   @media (max-width: 768px) {
    width:100%;
  }
`;

const Contentwrapper = styled.div`
  margin-left:15px;
  margin-top:15px;
  margin-right:15px;
  margin-bottom:15px;

`;

const Impactwrapper = styled.div`
  margin-left:0px;
  margin-top:15px;
  margin-right:0px;
  margin-bottom:15px;
    transform: scale(1);
  transition: all 0.5s;
      &:hover {
  transform: scale(1.04);
    transition: all 0.5s;
  
}
`;


class TakeAction extends Component{


  state = {
    sideDrawerOpen:false
  };

drawerToggleClickHandler = ()=> {

  this.setState((prevState) =>{
    return{sideDrawerOpen: !prevState.sideDrawerOpen};
});
};

backdropClickHandler = () => {
    this.setState({sideDrawerOpen:false});
};

  render(){

   
    let backdrop;

    if (this.state.sideDrawerOpen){
   
      backdrop= <Backdrop click={this.backdropClickHandler}/>
    }
    return(
      
        <body> 
          <Toolbar drawerClickHandler={this.drawerToggleClickHandler} />
          <SideDrawer show={this.state.sideDrawerOpen} />
          {backdrop}
          <SideMenuTakeAction />
          <NearSpaceBg/>
          
          <main className = "bod">
          
          <Stacker>
            
      <Contentwrapper>
          <Left>
           <Threat>  
           <Contentwrapper>
          <Tree>Take Action/</Tree>         
          </Contentwrapper>        
              <AskTakeAction/>
           </Threat>
          </Left>
      </Contentwrapper>
     
      <Contentwrapper>
          <Right>
           <Impacts>

              <Impactwrapper><Link to="/Take-Action"><InfoEatwell/></Link></Impactwrapper>       
              <Impactwrapper><Link to="/Take-Action"><InfoAvoidplastic/></Link></Impactwrapper>  
              <Impactwrapper><Link to="/Take-Action"><InfoInvestsmarter/></Link></Impactwrapper>
              <Impactwrapper><Link to="/Take-Action"><InfoKeepcool/></Link></Impactwrapper>
              <Impactwrapper><Link to="/Take-Action"><InfoProtectyourhome/></Link></Impactwrapper>
              <Impactwrapper><Link to="/Take-Action"><InfoSaveenergy/></Link></Impactwrapper>  
              <Impactwrapper><Link to="/Take-Action"><InfoVote/></Link></Impactwrapper>

           </Impacts>
          </Right> 
      </Contentwrapper>

      </Stacker>
          
          </main>
        </body>
      
      );

  } 
}

export default TakeAction;